
.team-lb-card {
  width: 100%;
  border-radius: 20px;
  -webkit-box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
  -webkit-transition: opacity 1s ease, -webkit-box-shadow 1s ease;
  transition: opacity 1s ease, -webkit-box-shadow 1s ease;
  transition: opacity 1s ease, box-shadow 1s ease;
  transition: opacity 1s ease, box-shadow 1s ease, -webkit-box-shadow 1s ease;
}
.team-lb-card.open {
  border-radius: 0;
  top: 0;
  left: 0;
  z-index: 1000 !important;
  -webkit-transition: opacity 0.2s ease, -webkit-box-shadow 0.2s ease;
  transition: opacity 0.2s ease, -webkit-box-shadow 0.2s ease;
  transition: opacity 0.2s ease, box-shadow 0.2s ease;
  transition: opacity 0.2s ease, box-shadow 0.2s ease, -webkit-box-shadow 0.2s ease;
  height: 100vh;
  overflow: scroll;
}
.team--title {
  color: #f1f1f1;
  background-color: #ACA885;
}




