
.hidden_row {
  display: none;
}
.putting-percent {
  font-weight: normal;
  color: #2E2F2F;
  font-size: 20px;
}
.putting-avg {
  color: #A8C256;
}
.board-table {
  overflow:hidden;
  border-radius: 0 0 20px 20px;
  -webkit-transition: opacity 0.2s ease, -webkit-box-shadow 0.2s ease;
  transition: opacity 0.2s ease, -webkit-box-shadow 0.2s ease;
  transition: opacity 0.2s ease, box-shadow 0.2s ease;
  transition: opacity 0.2s ease, box-shadow 0.2s ease, -webkit-box-shadow 0.2s ease;
}
.putting-table table.theme--light thead {
  background-color: #A8C256;
}
.putting-table table.theme--light thead tr th {
  color: #f1f1f1;
  font-size: 14px;
  letter-spacing: 1px;
}
table.table thead td:not(:nth-child(1)), table.table tbody td:not(:nth-child(1)), table.table thead th:not(:nth-child(1)), table.table tbody th:not(:nth-child(1)), table.table thead td:first-child, table.table tbody td:first-child, table.table thead th:first-child, table.table tbody th:first-child {
  padding: 0;
}



