
.stroke-percent {
  font-weight: normal;
  color: #2E2F2F;
  font-size: 20px;
}
.stroke-stats {
  color: #9FB8CE;
}
.board-table {
  overflow:hidden;
  border-radius: 0 0 20px 20px;
  -webkit-transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
