
a {
  text-decoration: none;
}
.admin--profile_button {
  background-color: #74C9D7;
  color: #FBFCFD;
  -webkit-box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
  -webkit-transition: opacity 2s ease, -webkit-box-shadow 2s ease;
  transition: opacity 2s ease, -webkit-box-shadow 2s ease;
  transition: opacity 2s ease, box-shadow 2s ease;
  transition: opacity 2s ease, box-shadow 2s ease, -webkit-box-shadow 2s ease;
}
.admin--profile_button:active {
  top: 1px;
  -webkit-box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
  -webkit-transition: opacity 2s ease, -webkit-box-shadow 1s ease;
  transition: opacity 2s ease, -webkit-box-shadow 1s ease;
  transition: opacity 2s ease, box-shadow 1s ease;
  transition: opacity 2s ease, box-shadow 1s ease, -webkit-box-shadow 1s ease;
}



