
.hide {
  display: none;
}
#round-container {
  margin: 4px 0;
  position: relative;
}
#round-container .open {
  z-index: 9999 !important;
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0;
  margin: 0;
  overflow: scroll;
}
.open {
  height: 100vh;
  top: 0;
  left: 0;
  position: fixed;
  z-index: 1000;
  overflow: scroll;
}
.mint-swipe-item .is-active .open {
  margin: 0;
  padding: 0;
}
.mint-swipe-items-wrap>div {
  padding-top: 20px;
  position: relative;
}
.mint-swipe .my-swipe {
  padding-top: 20px;
  color: #fff;
  font-size: 30px;
  text-align: center;
}
.mint-swipe .my-swipe .open {
  -webkit-transition: opacity 0.2s ease, -webkit-box-shadow 0.2s ease;
  transition: opacity 0.2s ease, -webkit-box-shadow 0.2s ease;
  transition: opacity 0.2s ease, box-shadow 0.2s ease;
  transition: opacity 0.2s ease, box-shadow 0.2s ease, -webkit-box-shadow 0.2s ease;
  width: 100%;
  margin: 0;
}
.rnd1 {
  color: white;
}
.expand {
  top: 0;
  left: 0;
  padding: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
}
.slide-enter-active, .slide-leave-active {
  -webkit-transition: all .8s ease-out;
  transition: all .8s ease-out;
}
.slide-enter, .slide-leave-to {
  -webkit-transition: all .8s ease-out;
  transition: all .8s ease-out;
}
.slide-enter-to, .slide-leave {
  -webkit-transition: all .8s ease-out;
  transition: all .8s ease-out;
}




