
.team-stats {
  color: #FE8202;
}
.board-table {
  overflow:hidden;
  border-radius: 0 0 20px 20px;
  -webkit-transition: opacity 0.2s ease, -webkit-box-shadow 0.2s ease;
  transition: opacity 0.2s ease, -webkit-box-shadow 0.2s ease;
  transition: opacity 0.2s ease, box-shadow 0.2s ease;
  transition: opacity 0.2s ease, box-shadow 0.2s ease, -webkit-box-shadow 0.2s ease;
}
.hidden_row {
  display: none;
}
.team-table table.theme--light thead {
  background-color: #ACA885;
}
.team-table table.theme--light thead tr th {
  color: #f1f1f1;
  font-size: 14px;
  letter-spacing: 1px;
}
table.v-table thead td:not(:nth-child(1)), table.v-table tbody td:not(:nth-child(1)), table.v-table thead th:not(:nth-child(1)), table.v-table tbody th:not(:nth-child(1)), table.v-table thead td:first-child, table.v-table tbody td:first-child, table.v-table thead th:first-child, table.v-table tbody th:first-child {
  padding: 1px;
}



