
.putting-lb-card {
  width: 100%;
  border-radius: 20px;
  -webkit-box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
}
.putting-lb-card.open {
  width: 100vw;
  border-radius: 0;
  top: 0;
  left: 0;
  z-index: 1000 !important;
  height: 100vh;
  overflow: scroll;
}
.putting--title {
  color: #f1f1f1;
  background-color: #A8C256;
}




