
.round-borders {
  border-radius: 20px;
  -webkit-box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
  -webkit-transition: opacity 0.2s ease, -webkit-box-shadow 0.2s ease;
  transition: opacity 0.2s ease, -webkit-box-shadow 0.2s ease;
  transition: opacity 0.2s ease, box-shadow 0.2s ease;
  transition: opacity 0.2s ease, box-shadow 0.2s ease, -webkit-box-shadow 0.2s ease;
}
.record {
  color: #6CADED;
}
.pers-record {
  color: #F79256;
}
label.scorecard-label {
  color: white;
  font-size: 10px;
}
div.card.scorecard_container {
  -webkit-box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
  -webkit-transition: all 600ms cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: all 600ms cubic-bezier(0.645, 0.045, 0.355, 1);
  -webkit-transition: all 600ms cubic-bezier(0.6, 0.04, 0.98, 0.335);
  transition: all 600ms cubic-bezier(0.6, 0.04, 0.98, 0.335);
  position: relative;
}
.v-card__title.scorecard-card.openCard {
  margin: 0;
  position: fixed;
  z-index: 9999;
  width: 100vw;
  -webkit-transition: all 600ms cubic-bezier(0.6, 0.04, 0.98, 0.335);
  transition: all 600ms cubic-bezier(0.6, 0.04, 0.98, 0.335);
}
.scorecard-card {
  padding: 0;
  position: relative;
  z-index: 1;
}
.slide-fade-enter-active {
  -webkit-transition: all .8s ease;
  transition: all .8s ease;
}
.slide-fade-leave-active {
  -webkit-transition: all .0s;
  transition: all .0s;
  opacity: 0;
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  -webkit-transform: translate(200px);
          transform: translate(200px);
  opacity: 0;
}


