
.scoring--stats {
  -webkit-transition: opacity 1s ease, -webkit-box-shadow 1s ease;
  transition: opacity 1s ease, -webkit-box-shadow 1s ease;
  transition: opacity 1s ease, box-shadow 1s ease;
  transition: opacity 1s ease, box-shadow 1s ease, -webkit-box-shadow 1s ease;
}
score-label.record {
  color: #F8C977;
}
.pers-record {
  color: #ED6C6C;
}
.personal {
  color: #999;
  font-size: 18px;
}
p.hide {
    display: none;
}
.name-list {
    color: #9ad3de;
}



